a {
  text-decoration: none;
  color: #E0ECFF;
}
* {
  box-sizing: border-box;
}
.airdrop-box {
  border-top: 1px solid #203B60;
  color: #FFFFFF;
  font-size: 18px;
  font-weight: normal;
}
.airdrop-box .airdrop-info {
  width: 100%;
  height: 786px;
  margin-top: 28px;
  background-image: url('./img/1568bg.jpg');
  background-repeat: no-repeat;
  padding-top: 119px;
  background-position: center;
}
.airdrop-box .airdrop-info .airdrop-info-box {
  width: 737px;
  height: 436px;
  margin: 0 auto;
  text-align: center;
}
.airdrop-box .airdrop-info .airdrop-info-box .airdrop-title {
  font-size: 56px;
  font-weight: 500;
}
.airdrop-box .airdrop-info .airdrop-info-box .wallet-address {
  margin-top: 45px;
}
.airdrop-box .airdrop-info .airdrop-info-box .wallet-address .blue-logo {
  display: inline-block;
  width: 7px;
  height: 10px;
  border-radius: 50%;
  background-color: #5577FD;
  margin-right: 6px;
}
.airdrop-box .airdrop-info .airdrop-info-box .wallet-balance {
  margin-top: 15px;
}
.airdrop-box .airdrop-info .airdrop-info-box .airdrop-time {
  width: 339px;
  height: 81px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  padding-left: 10px;
  margin-top: 56px;
}
.airdrop-box .airdrop-info .airdrop-info-box .airdrop-time .time-logo {
  width: 55px;
  height: 55px;
  background-color: #2564F4;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.airdrop-box .airdrop-info .airdrop-info-box .airdrop-time .time-text {
  text-align: left;
  display: flex;
  justify-content: space-between;
}
.airdrop-box .airdrop-info .airdrop-info-box .airdrop-time .time-text .time-title {
  margin-right: 10px;
}
.airdrop-box .airdrop-info .airdrop-info-box .position {
  margin-top: 20px;
}
.airdrop-box .airdrop-info .airdrop-info-box .position-text {
  justify-content: flex-start;
}
.airdrop-box .airdrop-info .airdrop-info-box .position-text .time-title {
  margin-left: 4px;
}
.airdrop-box .airdrop-info .airdrop-info-box .claim {
  display: flex;
  justify-content: center;
  margin-top: 44px;
}
.airdrop-box .airdrop-info .airdrop-info-box .claim div {
  color: #FFFFFF;
}
@media only screen and (min-width: 760px) and (max-width: 1140px) {
  .airdrop-box .airdrop-info {
    width: 100%;
    height: 585px;
    margin-top: 3px;
    background-image: url('./img/1140bg.jpg');
    padding-top: 81px;
  }
}
@media only screen and (min-width: 375px) and (max-width: 760px) {
  .airdrop-box .airdrop-info {
    width: 100%;
    height: 585px;
    margin-top: 3px;
    background-image: url('./img/760bg.jpg');
    padding-top: 81px;
  }
  .airdrop-box .airdrop-info .airdrop-info-box {
    width: 100%;
  }
}
@media only screen and (max-width: 375px) {
  .airdrop-box .airdrop-info {
    width: 100%;
    height: 100vh;
    margin-top: 3px;
    background-image: url('./img/375bg.jpg');
    padding-top: 81px;
  }
  .airdrop-box .airdrop-info .airdrop-info-box {
    width: 100%;
  }
  .airdrop-box .airdrop-info .airdrop-info-box .airdrop-title {
    text-align: start;
    padding-left: 28px;
    width: 259px;
  }
}
